










































































































































































{ Query } = require "~plugins/memory-record"

module.exports =
  mixins: [
    require("~plugins/get-by-mount") "1h", "story/oldlog"
    require('~plugins/pager')
      watch: (val, key)->
    require("~plugins/browser-store")
      replace:
        order:  "vid"
        folder_id: []
        yeary: []
        monthry: []
        upd_range: []
        upd_at: []
        sow_auth_id: []
        rating: []
        size: []
        say: []
        game: []
        option: []
        event: []
        discard: []
        config: []
      watch: ->
        @page_idxs = [0]
  ]

  data: ->
    mode: "oldlog"
    asc: "desc"
    drill: true

  methods:
    reset: ->
      @$router.replace query: {}
    
    book_url: (book_id, mode)->
      name: "sow-village-idx-mode"
      params: { mode, idx: [book_id, 0].join("-") }
      query: { pages: 1 }

    rating_img: (rating)->
      "#{env.STORE_URL}/images/icon/cd_#{rating}.png"

    submenu: (as)->
      @drill = ! @drill

    summary: (key)->
      @all.reduce?[key]

  computed:
    query_in: ->
      obj = {}
      for key in ["option", "event","discard","config"]
        continue unless @[key].length
        obj["card." + key] = @[key]
      obj

    query_where: ->
      obj = {}
      for key in ["folder_id","yeary","monthry","upd_range","upd_at","sow_auth_id","rating","size","say","game"]
        continue unless @[key].length
        obj["q." + key] = @[key]
      obj

    all: ->
      @read_at
      Query.sow_villages.mode @mode

    page_all_contents: ->
      @read_at
      Query.sow_villages.search @mode, @query_in, @query_where, @order, @asc
      .reduce.list




